import './index.scss';
import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Page from 'src/layout/Page';
import SEO from 'components/seo';
import { MemberMenu, MagazineMenu, BlogMenu, GithubMenu } from 'src/constants';
import { Jumbotron, Container, Row, Col, Button } from 'react-bootstrap';

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query {
      jumbotron: file(relativePath: { eq: "main/main_jumbo.jpg" }) {
        childImageSharp {
          fluid (maxWidth: 1920, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      memberImage: file(relativePath: { eq: "main/members.jpg" }) {
        childImageSharp {
          fluid (maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      magazineImage: file(relativePath: { eq: "main/magazines.jpg" }) {
        childImageSharp {
          fluid (maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      blogImage: file(relativePath: { eq: "main/blogs.png" }) {
        childImageSharp {
          fluid (maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      githubImage: file(relativePath: { eq: "main/github.png" }) {
        childImageSharp {
          fluid (maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  const mainJumboImage = `url(${data.jumbotron.childImageSharp.fluid.src})`;
  const mainContents = [
    {
      title: 'Lubycon',
      description: `
        <strong>Lubycon</strong>은 2014년에 결성된 개발자와 디자이너들의 토이 프로젝트 팀입니다. 멤버들은 일상 속의 아이디어나 새롭게 해보고 싶은 기술을 들고와서 다른 멤버들과 공유하고, 매일 새로운 것들에 도전하면서 프로젝트를 진행하고 있습니다.
      `,
      menu: MemberMenu,
      buttonTitle: '멤버들 보러가기',
      photo: `url(${data.memberImage.childImageSharp.fluid.src})`,
    },
    {
      title: '매거진',
      description: `
        <strong>Lubycon</strong>은 자체적인 매거진을 출판하고 있습니다. 이 매거진에는 멤버들이 평소에 나누던 생각이나 함께 프로젝트를 진행하며 생기는 다양한 에피소드들이 담겨 있습니다.
      `,
      menu: MagazineMenu,
      buttonTitle: '매거진 보러가기',
      photo: `url(${data.magazineImage.childImageSharp.fluid.src})`,
    },
    {
      title: '블로그 포스팅',
      description: `
        멤버들은 각자 블로그에도 포스팅을 하고 있습니다. <strong>Lubycon</strong>의 멤버들은 웹, 영상처리, 머신러닝 등 다양한 분야에서 활동하고 있기 때문에 다양한 내용과 견해에 대한 포스팅을 구경할 수 있습니다. 아래 버튼을 클릭하시면 멤버들의 블로그 포스팅을 한 눈에 보실 수 있습니다.
      `,
      menu: BlogMenu,
      buttonTitle: '멤버들 블로그 보러가기',
      photo: `url(${data.blogImage.childImageSharp.fluid.src})`,
    },
    {
      title: 'Lubycon의 프로젝트',
      description: `
        Lubycon의 프로젝트는 주로 Github를 사용하여 진행되며, 외부로 알려지고 싶지않은 프로젝트의 경우에는 Bitbucket을 사용합니다. 프로젝트는 하나의 서비스를 만드는 대형 프로젝트일 수도 있고 간단한 기능만 가진 어플리케이션을 개발하는 작은 프로젝트일 수도 있습니다. 
      `,
      menu: GithubMenu,
      buttonTitle: '루비콘 깃허브 구경하기',
      photo: `url(${data.githubImage.childImageSharp.fluid.src})`,
    }
  ];

  return (
    <Page pageName="main">
      <SEO title="Home" />
      <Jumbotron style={{ backgroundImage: mainJumboImage }}>
        <div className="dimmed"></div>
        <div className="contents">
          <h1>Hi, This is <strong>Lubycon</strong></h1>
          <p>Connect your creativity with the world</p>
        </div>
      </Jumbotron>
      <Container>
        {mainContents.map((content, index) => {
          return (
            <Row key={index} as="section">
              <Col as="h2" xs={12}>{content.title}</Col>
              <Col xs={{ span: 12, order: 1 }} md={{ span: 6, order: index % 2 ? 0 : 1 }} data-name="content">
                <p dangerouslySetInnerHTML={{ __html: content.description }}></p>
                <Button>
                  {content.menu.externalLink ? (
                    <a href={content.menu.link} target="_blank" rel="noopener noreferrer">{content.buttonTitle}</a>
                  ) : (
                    <Link to={content.menu.link}>{content.buttonTitle}</Link>
                  )}
                </Button>
              </Col>
              <Col xs={{ span: 12, order: 0 }} md={{ span: 6, order: index % 2 ? 1 : 0 }} data-name="decoration">
                <div style={{ backgroundImage: content.photo }}></div>
              </Col>
            </Row>
          );
        })}
      </Container>
    </Page>
  );
}

export default IndexPage
